import React, {useState, useEffect} from "react";
import { Dropdown, message} from 'antd';
import {Link} from 'react-router-dom';
import axios from 'axios'


export default function Header() {
  const onClick = ({ key }) => {
    message.info(`Click on item ${key}`);
  };

  const [category, setCategory] = useState([]);

    useEffect(() => {
        axios.get('get/')
        .then((response) => {
            setCategory(response.data)
        }).catch(() => {
            alert('Error in get')
        }, [])
    }
    )

  const items = category.map(item => {
  return {label: item.category_name,
  key: item.category_id
  }
  })

  return (
    <header>
      <div className="head">
        <div className="place">
          <div className="place_photo"></div>
          <p className="place_text">-</p>
        </div>
        <div className="logo"></div>
        <ul className="nav">
          <Link to = "/login">
            <li>
              <div className="welcome_photo"></div>
              <p></p>
            </li>
          </Link>
          <Link to = "/registration">
            <li>
              <p></p>
            </li>
          </Link>
          <li>
            <div className="category_photo"></div>

            <Dropdown menu={{ items, onClick }}>
              <p className="category_text"></p>
            </Dropdown>
          </li>
        </ul>
      </div>
      <div className="presentation"></div>
      <div className="welcome">    !        , , ,      .   -  ,       .   !</div>
    </header>
  );
}
